.main-nav {
  padding: 40px 0 40px;
  background-color: rgb(7 7 7);
  width: 100%;
  font-size: 2.1875rem;

  .info {
    color: $clr6;
    padding: 0 25px;
    display: flex;
    justify-content: space-evenly;

    a {
      color: $clr3;
      font-size: rem(25px);
      padding: 0 25px;
      list-style: none;
      text-decoration: none;
      cursor: pointer;
      &:hover {
        color: whitesmoke;
        cursor: pointer;
      }
    }
  }

  .last {
    align-self: center;
    font-size: rem(45px);
    background-color: $clr3;
    color: #1b2735;
    padding: 0 10px 0px 10px;
    font-weight: 800;
  }
}

@media screen and (min-width: 1080px) {
  .main-nav {
    font-size: rem(24px);
    a {
      margin: 20px;
    }
  }
}
