footer.main-footer {
  position: absolute;
  bottom: 0;
}

.main-footer-container {
  background-color: black;
  display: flex;
  justify-content: space-around;
  align-items: center;
  opacity: 0.8;
  padding: 60px 20px 60px;
  font-size: rem(18px);
  flex-wrap: wrap;

  span {
    font-size: rem(22px);
    color: $clr6;
  }

  .main-footer {
    a {
      display: flex;
      justify-content: center;
      flex-direction: row;
      text-decoration: none;
      color: $clr6;
      font-weight: 500;
      font-size: rem(22px);
      cursor: pointer;
      &:hover {
        font-weight: 800;
        color: rgb(164, 164, 182);
      }
    }
  }

  .footer-links {
    a {
      color: $clr6;
      margin-right: 22px;
      margin-left: 0;
      font-size: rem(40px);
      :hover {
        color: rgb(194, 195, 210);
      }
    }
  }
  .resume {
    color: $clr2;
    border: none;
    font-size: rem(16px);
    text-decoration: none;
    padding: 12px 22px;
    border-radius: 6px;
    width: fit-content;
    align-self: center;
    cursor: pointer;
    &:hover {
      background-color: rgba(240, 248, 255, 0.537);
    }
  }
}
