.project-section {
  color: $clr5;
  background: aliceblue;

  .header {
    text-align: center;
    font-size: rem(36px);
    color: rgb(31, 32, 33);
    margin-bottom: 25px;
    color: $clr5;

    span {
      font-size: rem(32px);
      color: $clr5;
    }
  }

  .projects {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    gap: 30px;
    margin: 0px 20px;

    .project {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 10px;
    }

    .text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      span {
        &:hover {
          color: $clr4;
          cursor: pointer;
          // font-size: rem(40px);
        }
      }
    }

    p {
      font-size: rem(20px);
      margin: 0;
    }

    img {
      width: 300px;
      height: 200px;
      border-radius: 15px;
      box-shadow: 2px 3px 8px 0 #58555a;
    }
  }

  .btn {
    display: flex;
    gap: 25px;

    button {
      border-radius: 6px;
      border: 3px solid $clr3;
      background-color: $clr1;
      box-shadow: 3px 3px 3px 0#c9c9c9;
      padding: 6px 10px;
      a {
        font-size: rem(20px);
        text-decoration: none;
        color: $clr2;
      }
      &:hover {
        a {
          color: $clr3;
        }
        background-color: $clr1;
        box-shadow: 3px 3px 3px 0#c9c9c9;
      }
    }
  }
  .up {
    position: fixed;
    text-decoration: none;
    bottom: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.325);
    background-color: #cbced160;
    font-size: rem(22px);
    text-align: center;
    justify-content: center;
    z-index: 100;

    a:visited {
      color: $clr2;
    }

    a:hover {
      color: $clr4;
    }
  }
}

@media screen and (max-width: 980px) {
  .project-section {
    .projects {
      grid-template-columns: 1fr 1fr;

      .header {
        font-size: rem(30px);
      }

      .project {
        .text {
          text-align: center;
          p {
            font-size: rem(14px);
          }

          img {
            width: 140px;
            height: 100px;
          }
        }
        .btn {
          gap: 20px;
          flex-direction: row;
          button {
            a {
              font-size: rem(14px);
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 390px) {
  .projects {
    .project {
      margin: 0px 20px;

      .text {
        display: block;
        p {
          padding: 0px 0 10px 0;
        }

        img {
          float: right;
          width: 130px;
          height: 100px;
        }
      }
      .btn {
        flex-direction: row;
        gap: 14px;
      }
    }
  }
}
