// COLOR PALETTE
$clr1: #6b6e7bba; // intro and email sign up background
$clr2: hsla(0, 0%, 9%, 0.948); //footer background
$clr3: hsla(219, 35%, 92%, 0.974); //main background
$clr4: hsla(0, 0%, 9%, 0.948); //testimonials background
$clr5: hsla(0, 0%, 9%, 0.948);
$clr6: hsla(219, 35%, 92%, 0.974);

// LAYOUT
$layoutPadding: 200px;
$headerLayoutPadding: 60px;

// LAYOUT BREAKPOINTS
$break-narrow: 375px; //mobile
$break-normal: 960px;
$break-wide: 1080px; // desktop

$padded-break-narrow: $break-narrow + $layoutPadding;
$padded-break-normal: $break-normal + $layoutPadding;
$padded-break-wide: $break-wide + $layoutPadding;
